* {
  box-sizing: border-box;
}

ul {
  list-style-type: none;
}

h2 {
  color: var(--dark-green, #1e2827);
  letter-spacing: -.64px;
  margin-bottom: 40px;
  font-family: Epilogue;
  font-size: 32px;
  font-style: normal;
  font-weight: 600;
  line-height: 40px;
}

h4 {
  color: var(--black, #141414);
  text-overflow: ellipsis;
  white-space: nowrap;
  letter-spacing: -.28px;
  width: 255px;
  margin: 0;
  padding: 0;
  font-family: Epilogue;
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
  line-height: 18px;
  overflow: hidden;
}

p {
  color: #14141480;
  letter-spacing: -.24px;
  margin: 0;
  padding: 0;
  font-family: Epilogue;
  font-size: 12px;
  font-style: normal;
  font-weight: 400;
  line-height: 14px;
}

.catalog {
  flex-direction: column;
  align-items: start;
  width: 335px;
  display: flex;
}

.catalog-list {
  flex-direction: column;
  align-items: start;
  gap: 40px;
  width: 100%;
  margin: 0;
  padding: 0;
  display: flex;
}

.catalog-description {
  flex-direction: column;
  align-items: start;
  gap: 4px;
  display: flex;
}

.flip-box {
  perspective: 1000px;
  background-color: #0000;
  justify-content: center;
  align-items: center;
  width: 336px;
  height: 360px;
  display: flex;
}

.flip-box-inner {
  text-align: center;
  transform-style: preserve-3d;
  width: 100%;
  height: 100%;
  transition: transform .8s;
  display: flex;
  position: relative;
}

.flip-box:hover .flip-box-inner {
  transform: rotateY(180deg);
}

.flip-box-front, .flip-box-back {
  backface-visibility: hidden;
  position: absolute;
}

.flip-box-front {
  color: #000;
  padding: 43px 30px;
}

.flip-box-back {
  color: #fff;
  background-color: #141414;
  border-radius: 6px;
  flex-direction: column;
  align-items: start;
  gap: 20px;
  width: 100%;
  height: 100%;
  padding: 46px 60px 41px 40px;
  display: flex;
  transform: rotateY(180deg);
}

.flip-box-back-description h4, .flip-box-back-details h4 {
  color: var(--white, #fff);
  text-align: start;
  letter-spacing: -.28px;
  text-transform: uppercase;
  margin: 0;
  padding: 0;
  font-family: Epilogue;
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
  line-height: 18px;
}

.flip-box-back-description p {
  color: #ffffff80;
  text-align: start;
  letter-spacing: -.14px;
  margin: 0;
  padding: 0 50px 0 0;
  font-family: Epilogue;
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
  line-height: 18px;
}

.flip-box-back-details ul {
  flex-direction: column;
  align-items: start;
  width: 100%;
  padding: 0;
  display: flex;
}

.flip-box-back-details ul li {
  color: #ffffff80;
  text-align: start;
  letter-spacing: -.14px;
  width: 100%;
  margin: 0;
  padding: 0;
  font-family: Epilogue;
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
  line-height: 18px;
}

.flip-box-back-description {
  flex-direction: column;
  align-items: start;
  gap: 10px;
  width: 236px;
  display: flex;
}

.flip-box-back-details {
  flex-direction: column;
  align-items: start;
  gap: 10px;
  width: 100%;
  display: flex;
}

.desktop {
  display: none;
}

.button-catalog {
  margin-top: 40px;
  margin-bottom: 80px;
  margin-left: 200px;
  display: flex;
}

@media only screen and (min-width: 768px) {
  .catalog {
    width: 768px;
    padding: 0 32px;
  }

  .catalog-list {
    flex-flow: wrap;
    gap: 0 32px;
  }

  .catalog-list li {
    margin-top: 40px;
  }

  .button-catalog {
    margin-top: 40px;
    margin-bottom: 128px;
    margin-left: 585px;
    display: flex;
  }

  .buy-btn {
    background-color: #0000;
    background: var(--dark-green, #1e2827);
    cursor: pointer;
    border-radius: 8px;
    outline: none;
    align-items: flex-start;
    gap: 10px;
    padding: 14px 28px;
    display: inline-flex;
  }

  .buy-btn:focus, .buy-btn:hover {
    box-shadow: 4px 6px 8px 3px #00000029, 0 4px 4px #0000000f, 0 1px 1px #0000001f;
  }

  .buy-span {
    color: var(--white, #fff);
    text-align: center;
    text-transform: uppercase;
    font-family: Epilogue;
    font-size: 12px;
    font-style: normal;
    font-weight: 400;
    line-height: 14px;
  }
}

@media only screen and (min-width: 1280px) {
  .catalog {
    justify-content: space-evenly;
    width: 100%;
    padding: 80px 60px;
  }

  .catalog-list {
    justify-content: start;
    gap: 0 24px;
    min-width: 1180px;
  }

  .catalog-list li {
    margin-top: 64px;
  }

  .desktop {
    display: initial;
  }

  .flip-box {
    width: 276px;
    height: 325px;
  }

  .flip-box-front {
    padding: 26px 0 25px;
  }

  .flip-box-back {
    padding: 20px;
  }

  .button-catalog {
    margin-top: 80px;
    margin-left: 1030px;
    display: flex;
  }

  .buy-btn {
    background-color: #0000;
    background: var(--dark-green, #1e2827);
    cursor: pointer;
    border-radius: 8px;
    outline: none;
    align-items: flex-start;
    gap: 10px;
    padding: 14px 28px;
    display: inline-flex;
  }

  .buy-btn:focus, .buy-btn:hover {
    box-shadow: 4px 6px 8px 3px #00000029, 0 4px 4px #0000000f, 0 1px 1px #0000001f;
  }

  .buy-span {
    color: var(--white, #fff);
    text-align: center;
    text-transform: uppercase;
    font-family: Epilogue;
    font-size: 12px;
    font-style: normal;
    font-weight: 400;
    line-height: 14px;
  }
}

